/**
 * Internal dependencies
 */
import Testimonial from "./testimonial";

/**
 * WordPress dependencies
 */
const { Component } = wp.element;
const { RichText } = wp.editor;

export default class Save extends Component {
    constructor() {
        super(...arguments);
    }

    render() {
        const {
            testimonialName,
            testimonialTitle,
            testimonialContent,
            testimonialAlignment,
            testimonialImgURL,
            testimonialTextColor,
            testimonialFontSize
        } = this.props.attributes;

        return (
            <Testimonial {...this.props}>
                <RichText.Content
                    tagName="div"
                    className="responsive-gutenberg-addons-testimonial-text"
                    style={{
                        textAlign: testimonialAlignment,
                            fontSize: testimonialFontSize,

        }}
                    value={testimonialContent}
                />

                <div className="responsive-gutenberg-addons-testimonial-info">
                    {testimonialImgURL && (
                        <div className="responsive-gutenberg-addons-testimonial-avatar-wrap">
                            <div className="responsive-gutenberg-addons-testimonial-image-wrap">
                                <img
                                    className="responsive-gutenberg-addons-testimonial-avatar"
                                    src={testimonialImgURL}
                                    alt="avatar"
                                />
                            </div>
                        </div>
                    )}

                    {testimonialName && (
                        <RichText.Content
                            tagName="h2"
                            className="responsive-gutenberg-addons-testimonial-name"
                            style={{
                                color: testimonialTextColor
                                    ? testimonialTextColor
                                    : "#32373c",
                                    fontSize: testimonialFontSize+10,

                    }}
                            value={testimonialName}
                        />
                    )}

                    {testimonialTitle && (
                        <RichText.Content
                            tagName="small"
                            className="responsive-gutenberg-addons-testimonial-title"
                            style={{
                                color: testimonialTextColor
                                    ? testimonialTextColor
                                    : "#32373c",
                                    fontSize: testimonialFontSize-3,

                    }}
                            value={testimonialTitle}
                        />
                    )}
                </div>
            </Testimonial>
        );
    }
}
