/**
 * Internal dependencies
 */
import classnames from "classnames"
import Inspector from "./inspector";
import times from "lodash/times";
import memoize from "memize"
import generateCSSUnit from "../generateCSSUnit";

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Component } = wp.element;
const { InnerBlocks } = wp.editor;
const ALLOWED_BLOCKS = [ 'responsive-gutenberg-addons/column' ];

const getColumnsTemplate = memoize( ( columns ) => {
    return times( columns, n => [ "responsive-gutenberg-addons/column", { id: n + 1 } ] )
} )


export default class Edit extends Component {
    constructor() {
        super(...arguments);
    }
    componentDidMount() {

        // Assigning block_id in the attribute.
        this.props.setAttributes( { block_id: this.props.clientId } )

        this.props.setAttributes( { classMigrate: true } )

        // Pushing Style tag for this block css.
        const $style = document.createElement( "style" )
        $style.setAttribute( "id", "responsive-columns-style-" + this.props.clientId )
        document.head.appendChild( $style )
    }

    render() {
        // Setup the attributes
        const {
            attributes: {
                columns,
                columnGap,
                contentWidth,
                width,
                widthType,
                stack,
                topPadding,
                bottomPadding,
                leftPadding,
                rightPadding,
                topMargin,
                bottomMargin,
                blockBorderStyle,
                blockBorderWidth,
                blockBorderRadius,
                blockBorderColor,
            },
            setAttributes
        } = this.props;

        let max_width = "100%"

        if ( "custom" == contentWidth ) {
            if ( "" != width ) {
                max_width = generateCSSUnit( width, widthType )
            }
        }

        return [

            // Show the block controls on focus
            <Inspector {...{ setAttributes, ...this.props }} />,

            // Show the block markup in the editor

        <div
        className={ classnames(
"responsive-gutenberg-addons-block-columns",
            "responsive-columns-wrap",
            `responsive-columns__gap-${columnGap}`,
            `responsive-columns__stack-${stack}`,
            `responsive-columns__content-width-${contentWidth}`,
    ) }
    style={{
        paddingTop: generateCSSUnit( topPadding, 'px' ),
        paddingBottom: generateCSSUnit( bottomPadding, 'px' ),
        paddingLeft: generateCSSUnit( leftPadding, 'px' ),
        paddingRight: generateCSSUnit( rightPadding, 'px' ),
        marginTop: generateCSSUnit( topMargin, 'px' ),
        marginBottom: generateCSSUnit( bottomMargin, 'px' ),
        borderWidth: generateCSSUnit( blockBorderWidth, 'px' ),
        borderColor: blockBorderColor,
        borderStyle: blockBorderStyle,
        borderRadius: blockBorderRadius,
        }}
        >

            <div className={ classnames(
                    "responsive-columns-inner-wrap",
                    `responsive-columns-columns-${columns}`
    ) }
style={{ maxWidth: max_width}}
            >
    <InnerBlocks
        template={ getColumnsTemplate( columns ) }
        templateLock="all"
        allowedBlocks={ ALLOWED_BLOCKS }
        />
            </div>

    </div>

    ];
    }
}
