/**
 * Inspector Controls
 */

// Setup the block
const { __ } = wp.i18n;
const { Component, Fragment } = wp.element;

import compact from "lodash/compact";
import map from "lodash/map";
import RenderSettingControl from "../../../utils/components/settings/renderSettingControl";

// Import block components
const { InspectorControls, ColorPalette } = wp.blockEditor;

// Import Inspector components
const {
    PanelBody,
    QueryControls,
    RangeControl,
    SelectControl,
    TextControl,
    ToggleControl
} = wp.components;

const { addQueryArgs } = wp.url;

const { apiFetch } = wp;

const MAX_POSTS_COLUMNS = 4;

/**
 * Create an Inspector Controls wrapper Component
 */
export default class Inspector extends Component {
    constructor() {
        super(...arguments);
        this.state = { categoriesList: [] };
    }

    componentDidMount() {
        this.stillMounted = true;
        this.fetchRequest = apiFetch({
            path: addQueryArgs("/wp/v2/categories", { per_page: -1 })
        })
            .then(categoriesList => {
                if (this.stillMounted) {
                    this.setState({ categoriesList });
                }
            })
            .catch(() => {
                if (this.stillMounted) {
                    this.setState({ categoriesList: [] });
                }
            });
    }

    componentWillUnmount() {
        this.stillMounted = false;
    }

    /* Get the available image sizes */
    imageSizeSelect() {
        const getSettings = wp.data.select("core/editor").getEditorSettings();

        return compact(
            map(getSettings.imageSizes, ({ name, slug }) => {
                return {
                    value: slug,
                    label: name
                };
            })
        );
    }

    render() {
        // Setup the attributes
        const { attributes, setAttributes, latestPosts } = this.props;

        const { order, orderBy } = attributes;

        const { categoriesList } = this.state;

        // Post type options
        const postTypeOptions = [
            { value: "post", label: __("Post", "responsive-gutenberg-addons") },
            { value: "page", label: __("Page", "responsive-gutenberg-addons") }
        ];

        // Section title tags
        const sectionTags = [
            { value: "div", label: __("div", "responsive-gutenberg-addons") },
            { value: "header", label: __("header", "responsive-gutenberg-addons") },
            { value: "section", label: __("section", "responsive-gutenberg-addons") },
            { value: "article", label: __("article", "responsive-gutenberg-addons") },
            { value: "main", label: __("main", "responsive-gutenberg-addons") },
            { value: "aside", label: __("aside", "responsive-gutenberg-addons") },
            { value: "footer", label: __("footer", "responsive-gutenberg-addons") }
        ];

        // Section title tags
        const sectionTitleTags = [
            { value: "h2", label: __("H2", "responsive-gutenberg-addons") },
            { value: "h3", label: __("H3", "responsive-gutenberg-addons") },
            { value: "h4", label: __("H4", "responsive-gutenberg-addons") },
            { value: "h5", label: __("H5", "responsive-gutenberg-addons") },
            { value: "h6", label: __("H6", "responsive-gutenberg-addons") }
        ];

        // Check for posts
        const hasPosts = Array.isArray(latestPosts) && latestPosts.length;

        // Check the post type
        const isPost = "post" === attributes.postType;

        // Add instruction text to the select
        const abImageSizeSelect = {
            value: "selectimage",
            label: __("Select image size")
        };

        // Get the image size options
        const imageSizeOptions = this.imageSizeSelect();

        imageSizeOptions.unshift(abImageSizeSelect);

        const imageSizeValue = () => {
            for (let i = 0; i < imageSizeOptions.length; i++) {
                if (imageSizeOptions[i].value === attributes.imageSize) {
                    return attributes.imageSize;
                }
            }
            return "full";
        };

        return (
            <InspectorControls>
                <PanelBody
                    title={__(
                        "Post and Page Grid Settings",
                        "responsive-gutenberg-addons"
                    )}
                    className={isPost ? null : "responsive-gutenberg-addons-hide-query"}
                >
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_postType">
                        <SelectControl
                            label={__("Content Type", "responsive-gutenberg-addons")}
                            options={postTypeOptions}
                            value={attributes.postType}
                            onChange={value =>
                                this.props.setAttributes({ postType: value })
                            }
                        />
                    </RenderSettingControl>
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_queryControls">
                        <QueryControls
                            {...{ order, orderBy }}
                            numberOfItems={attributes.postsToShow}
                            categoriesList={categoriesList}
                            selectedCategoryId={attributes.categories}
                            onOrderChange={value =>
                                setAttributes({ order: value })
                            }
                            onOrderByChange={value =>
                                setAttributes({ orderBy: value })
                            }
                            onCategoryChange={value =>
                                setAttributes({
                                    categories: "" !== value ? value : undefined
                                })
                            }
                            onNumberOfItemsChange={value =>
                                setAttributes({ postsToShow: value })
                            }
                        />
                    </RenderSettingControl>
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_offset">
                        <RangeControl
                            label={__(
                                "Number of items to offset",
                                "responsive-gutenberg-addons"
                            )}
                            value={attributes.offset}
                            onChange={value => setAttributes({ offset: value })}
                            min={0}
                            max={20}
                        />
                    </RenderSettingControl>
                    {"grid" === attributes.postLayout && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_columns">
                            <RangeControl
                                label={__("Columns", "responsive-gutenberg-addons")}
                                value={attributes.columns}
                                onChange={value =>
                                    setAttributes({ columns: value })
                                }
                                min={1}
                                max={
                                    !hasPosts
                                        ? MAX_POSTS_COLUMNS
                                        : Math.min(
                                              MAX_POSTS_COLUMNS,
                                              latestPosts.length
                                          )
                                }
                            />
                        </RenderSettingControl>
                    )}
                </PanelBody>
                <PanelBody
                    title={__(
                        "Post and Page Grid Content",
                        "responsive-gutenberg-addons"
                    )}
                    initialOpen={false}
                >
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displaySectionTitle">
                        <ToggleControl
                            label={__(
                                "Display Section Title",
                                "responsive-gutenberg-addons"
                            )}
                            checked={attributes.displaySectionTitle}
                            onChange={() =>
                                this.props.setAttributes({
                                    displaySectionTitle: !attributes.displaySectionTitle
                                })
                            }
                        />
                    </RenderSettingControl>
                    {attributes.displaySectionTitle && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_sectionTitle">
                            <TextControl
                                label={__("Section Title", "responsive-gutenberg-addons")}
                                type="text"
                                value={attributes.sectionTitle}
                                onChange={value =>
                                    this.props.setAttributes({
                                        sectionTitle: value
                                    })
                                }
                            />
                        </RenderSettingControl>
                    )}
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostImage">
                        <ToggleControl
                            label={__(
                                "Display Featured Image",
                                "responsive-gutenberg-addons"
                            )}
                            checked={attributes.displayPostImage}
                            onChange={() =>
                                this.props.setAttributes({
                                    displayPostImage: !attributes.displayPostImage
                                })
                            }
                        />
                    </RenderSettingControl>
                    {attributes.displayPostImage && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_imageSizeValue">
                            <SelectControl
                                label={__("Image Size", "responsive-gutenberg-addons")}
                                value={imageSizeValue()}
                                options={imageSizeOptions}
                                onChange={value =>
                                    this.props.setAttributes({
                                        imageSize: value
                                    })
                                }
                            />
                        </RenderSettingControl>
                    )}
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostTitle">
                        <ToggleControl
                            label={__("Display Title", "responsive-gutenberg-addons")}
                            checked={attributes.displayPostTitle}
                            onChange={() =>
                                this.props.setAttributes({
                                    displayPostTitle: !attributes.displayPostTitle
                                })
                            }
                        />
                    </RenderSettingControl>
                    {isPost && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostAuthor">
                            <ToggleControl
                                label={__(
                                    "Display Author",
                                    "responsive-gutenberg-addons"
                                )}
                                checked={attributes.displayPostAuthor}
                                onChange={() =>
                                    this.props.setAttributes({
                                        displayPostAuthor: !attributes.displayPostAuthor
                                    })
                                }
                            />
                        </RenderSettingControl>
                    )}
                    {isPost && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostDate">
                            <ToggleControl
                                label={__("Display Date", "responsive-gutenberg-addons")}
                                checked={attributes.displayPostDate}
                                onChange={() =>
                                    this.props.setAttributes({
                                        displayPostDate: !attributes.displayPostDate
                                    })
                                }
                            />
                        </RenderSettingControl>
                    )}
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostExcerpt">
                        <ToggleControl
                            label={__("Display Excerpt", "responsive-gutenberg-addons")}
                            checked={attributes.displayPostExcerpt}
                            onChange={() =>
                                this.props.setAttributes({
                                    displayPostExcerpt: !attributes.displayPostExcerpt
                                })
                            }
                        />
                    </RenderSettingControl>
                    {attributes.displayPostExcerpt && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_excerptLength">
                            <RangeControl
                                label={__(
                                    "Excerpt Length",
                                    "responsive-gutenberg-addons"
                                )}
                                value={attributes.excerptLength}
                                onChange={value =>
                                    setAttributes({ excerptLength: value })
                                }
                                min={0}
                                max={150}
                            />
                        </RenderSettingControl>
                    )}
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_displayPostLink">
                        <ToggleControl
                            label={__(
                                "Display Continue Reading Link",
                                "responsive-gutenberg-addons"
                            )}
                            checked={attributes.displayPostLink}
                            onChange={() =>
                                this.props.setAttributes({
                                    displayPostLink: !attributes.displayPostLink
                                })
                            }
                        />
                    </RenderSettingControl>
                    {attributes.displayPostLink && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_readMoreText">
                            <TextControl
                                label={__(
                                    "Customize Continue Reading Text",
                                    "responsive-gutenberg-addons"
                                )}
                                type="text"
                                value={attributes.readMoreText}
                                onChange={value =>
                                    this.props.setAttributes({
                                        readMoreText: value
                                    })
                                }
                            />
                        </RenderSettingControl>
                    )}
                </PanelBody>
                <PanelBody
                    title={__("Post and Page Grid Markup", "responsive-gutenberg-addons")}
                    initialOpen={false}
                    className="responsive-gutenberg-addons-block-post-grid-markup-settings"
                >
                    <RenderSettingControl id="responsive_gutenberg_addons_postgrid_sectionTag">
                        <SelectControl
                            label={__(
                                "Post Grid Section Tag",
                                "responsive-gutenberg-addons"
                            )}
                            options={sectionTags}
                            value={attributes.sectionTag}
                            onChange={value =>
                                this.props.setAttributes({ sectionTag: value })
                            }
                            help={__(
                                "Change the post grid section tag to match your content hierarchy.",
                                "responsive-gutenberg-addons"
                            )}
                        />
                    </RenderSettingControl>
                    {attributes.sectionTitle && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_sectionTitleTag">
                            <SelectControl
                                label={__(
                                    "Section Title Heading Tag",
                                    "responsive-gutenberg-addons"
                                )}
                                options={sectionTitleTags}
                                value={attributes.sectionTitleTag}
                                onChange={value =>
                                    this.props.setAttributes({
                                        sectionTitleTag: value
                                    })
                                }
                                help={__(
                                    "Change the post/page section title tag to match your content hierarchy.",
                                    "responsive-gutenberg-addons"
                                )}
                            />
                        </RenderSettingControl>
                    )}
                    {attributes.displayPostTitle && (
                        <RenderSettingControl id="responsive_gutenberg_addons_postgrid_postTitleTag">
                            <SelectControl
                                label={__(
                                    "Post Title Heading Tag",
                                    "responsive-gutenberg-addons"
                                )}
                                options={sectionTitleTags}
                                value={attributes.postTitleTag}
                                onChange={value =>
                                    this.props.setAttributes({
                                        postTitleTag: value
                                    })
                                }
                                help={__(
                                    "Change the post/page title tag to match your content hierarchy.",
                                    "responsive-gutenberg-addons"
                                )}
                            />
                        </RenderSettingControl>
                    )}
                </PanelBody>
                <PanelBody
                    title={__("Color", "responsive-gutenberg-addons")}
                    initialOpen={false}
                >
                    <p className="responsive-setting-label">
                        {__("Blog Post Background Color")}
                        <span className="components-base-control__label">
                            <span
                                className="component-color-indicator"
                                style={{ backgroundColor: attributes.bgColor }}
                            ></span>
                        </span>
                    </p>
                    <ColorPalette
                        value={attributes.bgColor}
                        onChange={value =>
                            this.props.setAttributes({
                                bgColor: value
                            })
                        }
                        allowReset
                    />
                    <p className="responsive-setting-label">
                        {__("Text Color")}
                        <span className="components-base-control__label">
                            <span
                                className="component-color-indicator"
                                style={{
                                    backgroundColor: attributes.textColor
                                }}
                            ></span>
                        </span>
                    </p>
                    <ColorPalette
                        value={attributes.textColor}
                        onChange={value =>
                            this.props.setAttributes({
                                textColor: value
                            })
                        }
                        allowReset
                    />
                </PanelBody>
                <PanelBody
                    title={__("Padding", "responsive-gutenberg-addons")}
                    initialOpen={false}
                >
                    <RenderSettingControl id="responsive_gutenberg_addons_inner_padding">
                        <RangeControl
                            label={__("Content Padding", "atomic-blocks")}
                            value={attributes.contentPadding}
                            onChange={value =>
                                this.props.setAttributes({
                                    contentPadding: value
                                })
                            }
                            min={0}
                            max={20}
                            step={1}
                        />
                    </RenderSettingControl>
                </PanelBody>
            </InspectorControls>
        );
    }
}
