/**
 * BLOCK: Responsive Blocks Testimonial
 */

// Import block dependencies and components
import Edit from "./components/edit";
import Save from "./components/save";

// Import CSS
import "./styles/style.scss";
import "./styles/styles.editor.scss";

// Internationalization
const { __ } = wp.i18n;

// Register block
const { registerBlockType } = wp.blocks;

// Register the block
registerBlockType("responsive-gutenberg-addons/testimonial", {
    title: __("Testimonial", "responsive-gutenberg-addons"),
    description: __(
        "Add a user testimonial with a name and title.",
        "responsive-gutenberg-addons"
    ),
    icon: "format-quote",
    category: "responsive_gutenberg_addons",
    keywords: [
        __("testimonial", "responsive-gutenberg-addons"),
        __("quote", "responsive-gutenberg-addons"),
        __("responsive", "responsive-gutenberg-addons")
    ],
    attributes: {
        testimonialName: {
            type: "array",
            selector: ".responsive-gutenberg-addons-testimonial-name",
            source: "children"
        },
        testimonialTitle: {
            type: "array",
            selector: ".responsive-gutenberg-addons-testimonial-title",
            source: "children"
        },
        testimonialContent: {
            type: "array",
            selector: ".responsive-gutenberg-addons-testimonial-text",
            source: "children"
        },
        testimonialAlignment: {
            type: "string"
        },
        testimonialImgURL: {
            type: "string",
            source: "attribute",
            attribute: "src",
            selector: "img"
        },
        testimonialImgID: {
            type: "number"
        },
        testimonialBackgroundColor: {
            type: "string",
            default: "#f2f2f2"
        },
        testimonialTextColor: {
            type: "string",
            default: "#32373c"
        },
        testimonialFontSize: {
            type: "number",
            default: 18
        },
        testimonialCiteAlign: {
            type: "string",
            default: "left-aligned"
        }
    },
    responsive_blocks_settings_data: {
        responsive_blocks_testimonial_testimonialFontSize: {
            title: __("Font Size", "responsive-gutenberg-addons")
        },
        responsive_blocks_testimonial_testimonialCiteAlign: {
            title: __("Cite Alignment", "responsive-gutenberg-addons")
        },
        responsive_blocks_testimonial_testimonialBackgroundColor: {
            title: __("Background Color", "responsive-gutenberg-addons")
        },
        responsive_blocks_testimonial_testimonialTextColor: {
            title: __("Text Color", "responsive-gutenberg-addons")
        }
    },

    /* Render the block in the editor. */
    edit: props => {
        return <Edit {...props} />;
    },

    /* Save the block markup. */
    save: props => {
        return <Save {...props} />;
    }
});
