/**
 * BLOCK: Responsive Blocks Post and Page Grid
 */

// Import block dependencies and components
import edit from "./components/edit";

// Import CSS
import "./styles/style.scss";
import "./styles/styles.editor.scss";

// Components
const { __ } = wp.i18n;

// Register block controls
const { registerBlockType } = wp.blocks;

// Register alignments
const validAlignments = ["center", "wide", "full"];

// Register the block
registerBlockType("responsive-gutenberg-addons/post-carousel", {
    title: __("Post Carousel", "responsive-gutenberg-addons"),
    description: __(
        "Display posts as a carousel.",
        "responsive-gutenberg-addons"
    ),
    icon: "slides",
    category: "responsive_gutenberg_addons",
    keywords: [
        __("slider", "responsive-gutenberg-addons"),
        __("posts", "responsive-gutenberg-addons"),
        __("blog", "responsive-gutenberg-addons"),
        __("latest", "responsive-gutenberg-addons"),
        __("carousel", "responsive-gutenberg-addons"),
        __("responsive", "responsive-gutenberg-addons")
    ],

    getEditWrapperProps(attributes) {
        const { align } = attributes;
        if (-1 !== validAlignments.indexOf(align)) {
            return { "data-align": align };
        }
    },

    edit,

    // Render via PHP
    save() {
        return null;
    }
});
