/**
 * BLOCK: Responsive Blocks Post and Page Grid
 */

// Import block dependencies and components
import edit from "./components/edit";

// Import CSS
import "./styles/style.scss";
import "./styles/styles.editor.scss";

// Components
const { __ } = wp.i18n;

// Register block controls
const { registerBlockType } = wp.blocks;

// Register alignments
const validAlignments = ["center", "wide", "full"];

// Register the block
registerBlockType("responsive-gutenberg-addons/responsive-gutenberg-addons-post-grid", {
    title: __("Responsive Post and Page Grid", "responsive-gutenberg-addons"),
    description: __(
        "Add a grid or list of customizable posts or pages.",
        "responsive-gutenberg-addons"
    ),
    icon: "grid-view",
    category: "responsive_gutenberg_addons",
    keywords: [
        __("post", "responsive-gutenberg-addons"),
        __("page", "responsive-gutenberg-addons"),
        __("grid", "responsive-gutenberg-addons")
    ],

    getEditWrapperProps(attributes) {
        const { align } = attributes;
        if (-1 !== validAlignments.indexOf(align)) {
            return { "data-align": align };
        }
    },

    edit,

    responsive_gutenberg_addons_settings_data: {
        responsive_gutenberg_addons_postgrid_postType: {
            title: __("Content Type", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_queryControls: {
            title: __("Query Controls", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_offset: {
            title: __("Post Offset", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_columns: {
            title: __("Columns", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displaySectionTitle: {
            title: __("Display Section Title", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_sectionTitle: {
            title: __("Section Title", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostImage: {
            title: __("Display Featured Image", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_imageSizeValue: {
            title: __("Image Size", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostTitle: {
            title: __("Display Post Title", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostAuthor: {
            title: __("Display Post Author", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostDate: {
            title: __("Display Post Date", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostExcerpt: {
            title: __("Display Post Excerpt", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_excerptLength: {
            title: __("Excerpt Length", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_displayPostLink: {
            title: __("Display Continue Reading Link", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_readMoreText: {
            title: __("Read More Text", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_sectionTag: {
            title: __("Post Grid Section Tag", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_sectionTitleTag: {
            title: __("Section Title Heading Tag", "responsive-gutenberg-addons")
        },
        responsive_gutenberg_addons_postgrid_postTitleTag: {
            title: __("Post Title Heading Tag", "responsive-gutenberg-addons")
        }
    },

    // Render via PHP
    save() {
        return null;
    }
});
