/**
 * External dependencies
 */

import isUndefined from "lodash/isUndefined";
import pickBy from "lodash/pickBy";
import moment from "moment";
import classnames from "classnames";
import Inspector from "./inspector";
import PostCarouselImage from "./image";
import Slider from "react-slick";
import map from "lodash/map"
import generateCSSUnit from "../generateCSSUnit";

const { compose } = wp.compose;

const { Component, Fragment } = wp.element;
const { RichText } = wp.editor;

const { __ } = wp.i18n;

const { decodeEntities } = wp.htmlEntities;

const { withSelect } = wp.data;

const { Placeholder, Spinner, Toolbar, Dashicon } = wp.components;

const { BlockAlignmentToolbar, BlockControls } = wp.blockEditor;

class LatestPostsBlock extends Component {

    render() {

        const { attributes, setAttributes, latestPosts

        } = this.props;

        const {
            displayPostExcerpt,
            displayPostDate,
            displayPostLink,
            excerptLength,
            columns,
            pauseOnHover,
            infiniteLoop,
            transitionSpeed,
            autoplay,
            autoplaySpeed,
            arrowDots,
            arrowSize,
            arrowBorderSize,
            arrowBorderRadius,
            arrowColor
        } = attributes;

        // Check if there are posts
        const hasPosts = Array.isArray(latestPosts) && latestPosts.length;

        // Check the post type
        const isPost = "post" === attributes.postType;

        if (!hasPosts) {
            return (
                <Fragment>
                    <Inspector {...{ setAttributes, ...this.props }} />
                    <Placeholder
                        icon="admin-post"
                        label={__(
                            "Post Carousel",
                            "responsive-gutenberg-addons"
                        )}
                    >
                        {!Array.isArray(latestPosts) ? (
                            <Spinner />
                        ) : (
                            __("No posts found.", "responsive-gutenberg-addons")
                        )}
                    </Placeholder>
                </Fragment>
            );
        }

        // Removing posts from display should be instant.
        const displayPosts =
            latestPosts.length > attributes.postsToShow
                ? latestPosts.slice(0, attributes.postsToShow)
                : latestPosts;


        // Get the section tag
        const SectionTag = attributes.sectionTag
            ? attributes.sectionTag
            : "section";

        // Get the section title tag
        const SectionTitleTag = attributes.sectionTitleTag
            ? attributes.sectionTitleTag
            : "h2";

        // Get the post title tag
        const PostTag = attributes.postTitleTag
            ? attributes.postTitleTag
            : "h3";


        const onRemoveImage = () => {

            x = null
            y=null
            setAttributes({
                x,
                y
            });
        };

        function NextArrow( props ) {
            return (
                <button type="button" data-role="none" className="slick-next slick-arrow" aria-label="Next" tabIndex="0" role="button" style={{ "borderColor" : arrowColor, "borderRadius" : arrowBorderRadius, "borderWidth" : arrowBorderSize }}>
        <Dashicon icon="arrow-right-alt2"/></button>
        )
        }

        function PrevArrow( props ) {
            return (
                <button type="button" data-role="none" className="slick-prev slick-arrow" aria-label="Previous" tabIndex="0" role="button" style={{ "borderColor" : arrowColor, "borderRadius" : arrowBorderRadius, "borderWidth" : arrowBorderSize }}>
        <Dashicon icon="arrow-left-alt2"/></button>
        )
        }

        let dots = ( "dots" == attributes.arrowDots  || "arrows_dots" == attributes.arrowDots  ) ? true : false
        let arrows = ( "arrows" == attributes.arrowDots  || "arrows_dots" == attributes.arrowDots  ) ? true : false

        const settings = {
            slidesToShow : attributes.columns,
            slidesToScroll : 1,
            autoplaySpeed : attributes.autoplaySpeed,
            autoplay : attributes.autoplay,
            infinite : attributes.infiniteLoop,
            pauseOnHover : pauseOnHover,
            speed : attributes.transitionSpeed,
            arrows : arrows,
            dots : dots,
            rtl : false,
            draggable: false,
            nextArrow: <NextArrow arrowSize={arrowSize}/>,
        prevArrow: <PrevArrow arrowSize={arrowSize}/>,
        responsive : [
            {
                breakpoint : 1024,
                settings : {
                    slidesToShow : attributes.tcolumns,
                    slidesToScroll : 1,
                }
            },
            {
                breakpoint : 767,
                settings : {
                    slidesToShow : attributes.mcolumns,
                    slidesToScroll : 1,
                }
            }
        ]
    }
        return (
            <Fragment>
                <Inspector {...{ setAttributes, ...this.props }}
        postCount={ latestPosts && latestPosts.length }

        />
                <BlockControls>
                    <BlockAlignmentToolbar
                        value={attributes.align}
                        onChange={value => {
                            setAttributes({ align: value });
                        }}
                        controls={["center", "wide", "full"]}
                    />
                </BlockControls>
                <SectionTag
                    className={classnames(
                        this.props.className,
                        "responsive-gutenberg-addons-block-post-carousel"
                    )}
                >

                    <div className="responsive-post-slick-carousel">
                        <Slider {...settings}>

                            {displayPosts.map((post, index) => (
<div>
                                <div className="responsive-gutenberg-addons-post-carousel-inner" style={{backgroundColor:attributes.bgColor}}>
                                <header className="responsive-gutenberg-addons-block-post-carousel-header">
                                    {attributes.displayPostTitle && (
                                        <PostTag className="responsive-gutenberg-addons-block-post-carousel-title">
                                            <a
                                                href={post.link}
                                                target="_blank"
                                                rel="bookmark"
                                                    >
                                                    {decodeEntities(
                                                            post.title.rendered.trim()
                                                ) ||
                                                __(
                                                    "(Untitled)",
                                                    "responsive-gutenberg-addons"
                                                )}
                                            </a>
                                        </PostTag>
                                    )}
                                    {isPost && (
                                    <div className="responsive-gutenberg-addons-block-post-carousel-byline">
                                        {attributes.displayPostAuthor &&
                                        post.author_info
                                            .display_name && (
                                        <div className="responsive-gutenberg-addons-block-post-carousel-author">
                                        <a
                                        className="responsive-gutenberg-addons-text-link"
                                        target="_blank"
                                        href={
                                            post
                                            .author_info
                                            .author_link
                                    }
                                    >
                                        {
                                            post
                                                .author_info
                                                .display_name
                                        }
                                    </a>
                                    </div>
                                    )}

                                        {attributes.displayPostDate &&
                                        post.date_gmt && (
                                        <time
                                            dateTime={moment(
                                                    post.date_gmt
                                        )
                                        .utc()
                                            .format()}
                                            className={
                                                    "responsive-gutenberg-addons-block-post-carousel-date"
                                                }
                                                >
                                                {moment(
                                                        post.date_gmt
                                        )
                                        .local()
                                            .format(
                                                "MMMM DD, Y",
                                                "responsive-gutenberg-addons"
                                            )}
                                        </time>
                                        )}
                                    </div>
                                    )}
                                </header>
                                <div
                                    className="responsive-gutenberg-addons-block-post-carousel-excerpt"
                                    style={{
                                    color: attributes.textColor
                                        ? attributes.textColor
                                        : "#e4e4e4"
                                }}
                                >
                                    {attributes.displayPostExcerpt &&
                                    post.excerpt && (
                                    <div
                                        dangerouslySetInnerHTML={{
                                        __html: truncate(
                                            post.excerpt
                                                .rendered,
                                            attributes.excerptLength
                                        )
                                    }}
                                        />
                                    )}

                                    {attributes.displayPostLink && (
                                    <p>
                                    <a
                                        className="responsive-gutenberg-addons-block-post-carousel-more-link responsive-gutenberg-addons-text-link"
                                        href={post.link}
                                        target="_blank"
                                        rel="bookmark"
                                            >
                                            {attributes.readMoreText}
                                    </a>
                                    </p>
                                    )}
                                </div>
                                </div>
                                </div>
                            ))}
                        </Slider>
                    </div>
                </SectionTag>
            </Fragment>
        );
    }
}

export default compose([
    withSelect((select, props) => {
        const { order, categories } = props.attributes;

        const { getEntityRecords } = select("core");

        const latestPostsQuery = pickBy(
            {
                categories,
                order,
                orderby: props.attributes.orderBy,
                per_page: props.attributes.postsToShow,
                offset: props.attributes.offset,
                exclude: [wp.data.select("core/editor").getCurrentPostId()]
            },
            value => !isUndefined(value)
        );

        return {
            latestPosts: getEntityRecords(
                "postType",
                props.attributes.postType,
                latestPostsQuery
            )
        };
    })
])(LatestPostsBlock);

// Truncate excerpt
function truncate(str, no_words) {
    return str
        .split(" ")
        .splice(0, no_words)
        .join(" ");
}
