/**
 * Inspector Controls
 */

// Setup the block
const { __ } = wp.i18n;
const { Component,Fragment } = wp.element;
const { ColorPalette } = wp.blockEditor;

// Import block components
const { InspectorControls } = wp.editor;

// Import Inspector components
const { PanelBody, RangeControl, SelectControl, ButtonGroup, Button } = wp.components;

/**
 * Create an Inspector Controls wrapper Component
 */
export default class Inspector extends Component {
    constructor(props) {
        super(...arguments);
    }

    render() {
        // Setup the attributes
        const {
            attributes: {
                columns,
                columnGap,
                contentWidth,
                width,
                widthType,
                stack,
                topPadding,
                bottomPadding,
                leftPadding,
                rightPadding,
                topMargin,
                blockBorderStyle,
                blockBorderWidth,
                blockBorderRadius,
                blockBorderColor,

            },
            setAttributes
        } = this.props;

        return (
            <InspectorControls key="inspector">

                <PanelBody
                    title={__("General", "responsive-gutenberg-addons")}
                    initialOpen={false}>

                    <RangeControl
                            label={ __( "Columns", "responsive-gutenberg-addons" )}
                            value={ columns }
                            min={ 0 }
                            max={ 6 }
                            onChange={ ( value ) => setAttributes( { columns: value } ) }
                    />

                    <SelectControl
                        label={ __( "Column Gap", "responsive-gutenberg-addons" ) }
                        value={ columnGap }
                        onChange={ ( value ) => setAttributes( { columnGap: value } ) }
                        options={ [
                                { value: "default", label: __( "Default (10px)", "responsive-gutenberg-addons" ) },
                                { value: "nogap", label: __( "No Gap (0px)", "responsive-gutenberg-addons" ) },
                                { value: "narrow", label: __( "Narrow (5px)", "responsive-gutenberg-addons" ) },
                                { value: "extended", label: __( "Extended (15px)", "responsive-gutenberg-addons" ) },
                                { value: "wide", label: __( "Wide (20px)", "responsive-gutenberg-addons" ) },
                                { value: "wider", label: __( "Wider (30px)", "responsive-gutenberg-addons" ) }
                        ] }
                        help={ __( "Note: The individual Column Gap can be managed from Column Settings.", "responsive-gutenberg-addons" ) }
                    />
                    <SelectControl
                        label={ __( "Stack on", "responsive-gutenberg-addons" ) }
                        value={ stack }
                        options={ [
                                { value: "none", label: __( "None", "responsive-gutenberg-addons" ) },
                        { value: "tablet", label: __( "Tablet", "responsive-gutenberg-addons" ) },
                        { value: "mobile", label: __( "Mobile", "responsive-gutenberg-addons" ) },
                    ] }
                        onChange={ ( value ) => setAttributes( { stack: value } ) }
                        help={ __( "Note: Choose on what breakpoint the columns will stack.", "responsive-gutenberg-addons" ) }
                    />
                    <SelectControl
                        label={ __( "Container Width", "responsive-gutenberg-addons" ) }
                        value={ contentWidth }
                        onChange={ ( value ) => setAttributes( { contentWidth: value } ) }
                        options={ [
                                { value: "theme", label: __( "Theme Container Width", "responsive-gutenberg-addons" ) },
                        { value: "custom", label: __( "Custom", "responsive-gutenberg-addons" ) },
                        ] }
                    />
                        {
                            contentWidth == "custom" &&
                            (
                            <Fragment>
                            <ButtonGroup className="responsive-size-type-field" aria-label={ __( "Size Type", "responsive-gutenberg-addons" ) }>
                            <Button key={ "px" } className="responsive-size-btn" isSmall isPrimary={ widthType === "px" } aria-pressed={ widthType === "px" } min={0} max={2000} onClick={ () => setAttributes( { widthType: "px" } ) }>{ "px" }</Button>
                            <Button key={ "%" } className="responsive-size-btn" isSmall isPrimary={ widthType === "%" } aria-pressed={ widthType === "%" } min={0} max={100} onClick={ () => setAttributes( { widthType: "%" } ) }>{ "%" }</Button>
                            </ButtonGroup>
                            <RangeControl
                                label={ __( "Inner Width", "responsive-gutenberg-addons" ) }
                                value={ width }
                                min={ 0 }
                                max={ ( "%" == widthType ) ? 100 : 2000 }
                                onChange={ ( value ) => setAttributes( { width: value } ) }
                                />
                                </Fragment>
                            )
                        }


                </PanelBody>
                <PanelBody
                    title={__("Spacing", "responsive-gutenberg-addons")}
                    initialOpen={false}>

                    <RangeControl
                        label={ __("Top Padding", "responsive-gutenberg-addons") }
                        value={ topPadding }
                        onChange={ ( value ) => setAttributes( { topPadding: value } ) }
                        min={ 0 }
                        max={ 2000 }
                        allowReset
                    />
                    <RangeControl
                        label={ __("Bottom Padding", "responsive-gutenberg-addons") }
                        value={ bottomPadding }
                        onChange={ ( value ) => setAttributes( { bottomPadding: value } ) }
                        min={ 0 }
                        max={ 2000 }
                        allowReset
                    />
                    <RangeControl
                        label={ __("Left Padding", "responsive-gutenberg-addons") }
                        value={ leftPadding }
                        onChange={ ( value ) => setAttributes( { leftPadding: value } ) }
                        min={ 0 }
                        max={ 2000 }
                        allowReset
                    />
                    <RangeControl
                        label={ __("Right Padding", "responsive-gutenberg-addons") }
                        value={ rightPadding }
                        onChange={ ( value ) => setAttributes( { rightPadding: value } ) }
                        min={ 0 }
                        max={ 2000 }
                        allowReset
                    />
                    <RangeControl
                        label={ __("Top Margin", "responsive-gutenberg-addons") }
                        value={ topMargin }
                        onChange={ ( value ) => setAttributes( { topMargin: value } ) }
                        min={ -2000 }
                        max={ 2000 }
                        allowReset
                    />
                    <RangeControl
                        label={ __("Top Margin", "responsive-gutenberg-addons") }
                        value={ topMargin }
                        onChange={ ( value ) => setAttributes( { topMargin: value } ) }
                        min={ -2000 }
                        max={ 2000 }
                        allowReset
                    />

                </PanelBody>
                <PanelBody
                    title={__("Border", "responsive-gutenberg-addons")}
                    initialOpen={false}>
                        <SelectControl
                            label={ __( "Border Style" ) }
                            value={ blockBorderStyle }
                            onChange={ ( value ) => setAttributes( { blockBorderStyle: value } ) }
                            options={ [
                                    { value: "none", label: __( "None" ) },
                            { value: "solid", label: __( "Solid" ) },
                            { value: "dotted", label: __( "Dotted" ) },
                            { value: "dashed", label: __( "Dashed" ) },
                            { value: "double", label: __( "Double" ) },
                            { value: "groove", label: __( "Groove" ) },
                            { value: "inset", label: __( "Inset" ) },
                            { value: "outset", label: __( "Outset" ) },
                            { value: "ridge", label: __( "Ridge" ) },
                        ] }
                        />
                        { "none" != blockBorderStyle && (
                        <RangeControl
                            label={ __( "Border Width" ) }
                            value={ blockBorderWidth }
                            onChange={ ( value ) => setAttributes( { blockBorderWidth: value } ) }
                            min={ 0 }
                            max={ 50 }
                            allowReset
                            />
                        ) }
                        <RangeControl
                            label={ __( "Border Radius" ) }
                            value={ blockBorderRadius }
                            onChange={ ( value ) => setAttributes( { blockBorderRadius: value } ) }
                            min={ 0 }
                            max={ 1000 }
                            allowReset
                        />
                        { "none" != blockBorderStyle && (
                        <Fragment>
                            <p className="responsive-setting-label">{ __( "Border Color" ) }<span className="components-base-control__label"><span className="component-color-indicator" style={{ backgroundColor: blockBorderColor }} ></span></span></p>
                            <ColorPalette
                            value={ blockBorderColor }
                            onChange={ ( colorValue ) => setAttributes( { blockBorderColor: colorValue } ) }
                            allowReset
                            />
                        </Fragment>

                        ) }

            </PanelBody>

        </InspectorControls>
        );
    }
}
