/**
 * BLOCK: Responsive Blocks Advance Columns
 */

// Import block dependencies and components
import Edit from "./components/edit";
import Save from "./components/save";

// Import CSS
import "./styles/style.scss";
import "./styles/styles.editor.scss";

// Internationalization
const { __ } = wp.i18n;

// Register block
const { registerBlockType } = wp.blocks;

// Register the block
registerBlockType("responsive-gutenberg-addons/advance-columns", {
        title: __("Advance Columns", "responsive-gutenberg-addons"),
    description: __(
        "This block gives you advanced options to insert a number of columns within a single row.",
        "responsive-gutenberg-addons"
    ),
    icon: "layout",
    category: "responsive_gutenberg_addons",
    keywords: [
        __("calendar", "responsive-gutenberg-addons"),
        __("date", "responsive-gutenberg-addons"),
        __("responsive", "responsive-gutenberg-addons")
    ],
    attributes: {
        columns: {
            type: "number",
            default: 2
        },
        columnGap: {
            type: "string",
            default: "default"
        },
        width: {
            type: "number",
            default: 900
        },
        widthType: {
            type: "string",
            default: "px"
        },
        contentWidth: {
            type: "string",
            default: "theme"
        },
        stack: {
            type: "string",
            default: "mobile"
        },
        topPadding: {
            type: "number",
            default: 20
        },
        bottomPadding: {
            type: "number",
            default: 20
        },
        leftPadding: {
            type: "number",
            default: 20
        },
        rightPadding: {
            type: "number",
            default: 20
        },
        topMargin: {
            type: "number",
            default: 0
        },
        blockBorderStyle : {
            type: "string",
            default: "none"
        },
        blockBorderWidth : {
            type: "number",
            default: 1
        },
        blockBorderRadius : {
            type: "number"
        },
        blockBorderColor : {
            type: "string"
        }
    },

    /* Render the block in the editor. */
    edit: props => {
        return <Edit {...props} />;
    },

    /* Save the block markup. */
    save: props => {
        return <Save {...props} />;
    }
});
