/**
 * Inspector Controls
 */

// Setup the block
const { __ } = wp.i18n;
const { Component, Fragment } = wp.element;

import compact from "lodash/compact";
import map from "lodash/map";
import RenderSettingControl from "../../../utils/components/settings/renderSettingControl";

// Import block components

const { InspectorControls, ColorPalette } = wp.blockEditor;

// Import Inspector components
const {
    PanelBody,
    QueryControls,
    RangeControl,
    SelectControl,
    TextControl,
    ToggleControl
} = wp.components;

const { addQueryArgs } = wp.url;

const { apiFetch } = wp;

const MAX_POSTS_COLUMNS = 4;

/**
 * Create an Inspector Controls wrapper Component
 */
export default class Inspector extends Component {
    constructor() {
        super(...arguments);
        this.state = { categoriesList: [] };
        this.togglePauseOnHover = this.togglePauseOnHover.bind( this )
        this.toggleInfiniteLoop = this.toggleInfiniteLoop.bind( this )
        this.toggleAutoplay     = this.toggleAutoplay.bind( this )

    }

    componentDidMount() {
        this.stillMounted = true;
        this.fetchRequest = apiFetch({
            path: addQueryArgs("/wp/v2/categories", { per_page: -1 })
        })
            .then(categoriesList => {
                if (this.stillMounted) {
                    this.setState({ categoriesList });
                }
            })
            .catch(() => {
                if (this.stillMounted) {
                    this.setState({ categoriesList: [] });
                }
            });
    }

    componentWillUnmount() {
        this.stillMounted = false;
    }

    /* Get the available image sizes */
    imageSizeSelect() {
        const getSettings = wp.data.select("core/editor").getEditorSettings();

        return compact(
            map(getSettings.imageSizes, ({ name, slug }) => {
                return {
                    value: slug,
                    label: name
                };
            })
        );
    }

    togglePauseOnHover() {
        const { pauseOnHover } = this.props.attributes
        const { setAttributes } = this.props

        setAttributes( { pauseOnHover: ! pauseOnHover } )
    }

    toggleInfiniteLoop() {
        const { infiniteLoop } = this.props.attributes
        const { setAttributes } = this.props

        setAttributes( { infiniteLoop: ! infiniteLoop } )
    }

    toggleAutoplay() {
        const { autoplay } = this.props.attributes
        const { setAttributes } = this.props

        setAttributes( { autoplay: ! autoplay } )
    }


    render() {
        // Setup the attributes
        const { attributes, setAttributes, latestPosts, postCount } = this.props;
        const {
            displayPostExcerpt,
            displayPostDate,
            displayPostLink,
            excerptLength,
            columns,
            pauseOnHover,
            infiniteLoop,
            transitionSpeed,
            autoplay,
            autoplaySpeed,
            arrowDots,
            arrowSize,
            arrowBorderSize,
            arrowBorderRadius

        } = attributes;

        const { order, orderBy } = attributes;

        const { categoriesList } = this.state;

        const columnsCountOnChange = ( selectedColumns ) => {
            const { postsToShow } = attributes;
            setAttributes( { columns:
                    ( selectedColumns > postsToShow ) ? postsToShow : selectedColumns,
            } );
        };
        const postsCountOnChange = ( selectedPosts ) => {
            const { columns } = attributes;
            const changedAttributes = { postsToShow: selectedPosts };
            if ( columns > selectedPosts || ( selectedPosts === 1 && columns !== 1 ) ) {
                Object.assign( changedAttributes, { columns: selectedPosts } );
            }
            setAttributes( changedAttributes );
        };


        // Post type options
        const postTypeOptions = [
            { value: "post", label: __("Post", "responsive-gutenberg-addons") },
            { value: "page", label: __("Page", "responsive-gutenberg-addons") }
        ];

        // Section title tags
        const sectionTags = [
            { value: "div", label: __("div", "responsive-gutenberg-addons") },
            { value: "header", label: __("header", "responsive-gutenberg-addons") },
            { value: "section", label: __("section", "responsive-gutenberg-addons") },
            { value: "article", label: __("article", "responsive-gutenberg-addons") },
            { value: "main", label: __("main", "responsive-gutenberg-addons") },
            { value: "aside", label: __("aside", "responsive-gutenberg-addons") },
            { value: "footer", label: __("footer", "responsive-gutenberg-addons") }
        ];

        // Section title tags
        const sectionTitleTags = [
            { value: "h2", label: __("H2", "responsive-gutenberg-addons") },
            { value: "h3", label: __("H3", "responsive-gutenberg-addons") },
            { value: "h4", label: __("H4", "responsive-gutenberg-addons") },
            { value: "h5", label: __("H5", "responsive-gutenberg-addons") },
            { value: "h6", label: __("H6", "responsive-gutenberg-addons") }
        ];

        // Check for posts
        const hasPosts = Array.isArray(latestPosts) && latestPosts.length;

        // Check the post type
        const isPost = "post" === attributes.postType;

        // Add instruction text to the select
        const abImageSizeSelect = {
            value: "selectimage",
            label: __("Select image size")
        };

        // Get the image size options
        const imageSizeOptions = this.imageSizeSelect();

        imageSizeOptions.unshift(abImageSizeSelect);

        const imageSizeValue = () => {
            for (let i = 0; i < imageSizeOptions.length; i++) {
                if (imageSizeOptions[i].value === attributes.imageSize) {
                    return attributes.imageSize;
                }
            }
            return "full";
        };

        return (
            <InspectorControls>
                        <PanelBody title={ __( 'General', 'responsive-gutenberg-addons' ) }
        initialOpen={false}>
    <Fragment>
            <ToggleControl
        label={__("Post Title", "responsive-gutenberg-addons")}
        checked={attributes.displayPostTitle}
        help={
            attributes.displayPostTitle ?
            __( 'Showing the post title.', 'responsive-gutenberg-addons' ) :
            __( 'Toggle to show the post title.', 'responsive-gutenberg-addons' )
    }

        onChange={() =>
        this.props.setAttributes({
            displayPostTitle: !attributes.displayPostTitle
        })
    }
        />
            <ToggleControl
            label={__(
                "Post Author",
                "responsive-gutenberg-addons"
        )}
            checked={attributes.displayPostAuthor}
        help={
            attributes.displayPostAuthor ?
            __( 'Showing the post author.', 'responsive-gutenberg-addons' ) :
            __( 'Toggle to show the post author.', 'responsive-gutenberg-addons' )
    }

        onChange={() =>
            this.props.setAttributes({
                displayPostAuthor: !attributes.displayPostAuthor
            })
        }
            />
        <ToggleControl
        label={ __( 'Post date', 'responsive-gutenberg-addons' ) }
        checked={ attributes.displayPostDate }
        help={
            attributes.displayPostDate ?
                __( 'Showing the publish date.', 'responsive-gutenberg-addons' ) :
                __( 'Toggle to show the publish date.', 'responsive-gutenberg-addons' )
        }
        onChange={ () => setAttributes( { displayPostDate: ! displayPostDate } ) }
        />
        <ToggleControl
        label={ __( 'Post Excerpt', 'responsive-gutenberg-addons' ) }
        checked={ attributes.displayPostExcerpt }
        help={
            attributes.displayPostExcerpt ?
                __( 'Showing the post content.', 'responsive-gutenberg-addons' ) :
                __( 'Toggle to show the post content.', 'responsive-gutenberg-addons' )
        }
        onChange={ () => setAttributes( { displayPostExcerpt: ! displayPostExcerpt } ) }
        />
        <ToggleControl
        label={ __( 'Post Link', 'responsive-gutenberg-addons' ) }
        checked={ attributes.displayPostLink }
        help={
            attributes.displayPostLink ?
                __( 'Showing the post link.', 'responsive-gutenberg-addons' ) :
                __( 'Toggle to show the post link.', 'responsive-gutenberg-addons' )
        }
        onChange={ () => setAttributes( { displayPostLink: ! displayPostLink } ) }
        />
        { attributes.displayPostExcerpt &&
        <RangeControl
            label={ __( 'Excerpt Length', 'responsive-gutenberg-addons' ) }
            value={ excerptLength }
            onChange={ ( value ) => setAttributes( { excerptLength: value } ) }
            min={ 5 }
            max={ 75 }
            />
        }
    <RangeControl
        label={ __( 'Columns', 'responsive-gutenberg-addons' ) }
        value={ columns }
        onChange={ ( value ) => columnsCountOnChange( value ) }
        min={ 1 }
        max={ Math.min( 4, postCount ) }
        required
        />
        <RenderSettingControl id="responsive_gutenberg_addons_postcarousel_queryControls">
            <QueryControls
        {...{ order, orderBy }}
        numberOfItems={attributes.postsToShow}
        categoriesList={categoriesList}
        selectedCategoryId={attributes.categories}
        onOrderChange={value =>
        setAttributes({ order: value })
    }
        onOrderByChange={value =>
        setAttributes({ orderBy: value })
    }
        onCategoryChange={value =>
        setAttributes({
            categories: "" !== value ? value : undefined
        })
    }
        onNumberOfItemsChange={value =>
        setAttributes({ postsToShow: value })
    }
        />
        </RenderSettingControl>

        </Fragment>
        </PanelBody>
        <PanelBody
        title={__("Carousel", "responsive-gutenberg-addons")}
        initialOpen={false}>
            <ToggleControl
        label={ __( "Pause On Hover" ) }
        checked={ pauseOnHover }
        onChange={ this.togglePauseOnHover }
        />
        <ToggleControl
        label={ __( "Autoplay" ) }
        checked={ autoplay }
        onChange={ this.toggleAutoplay }
        />
        { autoplay == true &&
        <RangeControl
            label={ __( "Autoplay Speed (ms)" ) }
            value={ autoplaySpeed }
            onChange={ ( value ) => setAttributes( { autoplaySpeed: value } ) }
            min={ 100 }
            max={ 10000 }
            />
        }
    <ToggleControl
        label={ __( "Infinite Loop" ) }
        checked={ infiniteLoop }
        onChange={ this.toggleInfiniteLoop }
        />
        <RangeControl
        label={ __( "Transition Speed (ms)" ) }
        value={ transitionSpeed }
        onChange={ ( value ) => setAttributes( { transitionSpeed: value } ) }
        min={ 100 }
        max={ 5000 }
        />
        <SelectControl
        label={ __( "Show Arrows & Dots" ) }
        value={ arrowDots }
        onChange={ ( value ) => setAttributes( { arrowDots: value } ) }
        options={ [
                { value: "arrows", label: __( "Only Arrows" ) },
        { value: "dots", label: __( "Only Dots" ) },
        { value: "arrows_dots", label: __( "Both Arrows & Dots" ) },
    ] }
        />
        { "dots" != arrowDots &&
        <Fragment>
        <RangeControl
            label={ __( "Arrow Size" ) }
            value={ arrowSize }
            onChange={ ( value ) => setAttributes( { arrowSize: value } ) }
            min={ 0 }
            max={ 50 }
            />
            <RangeControl
            label={ __( "Arrow Border Size" ) }
            value={ arrowBorderSize }
            onChange={ ( value ) => setAttributes( { arrowBorderSize: value } ) }
            min={ 0 }
            max={ 50 }
            />
            <RangeControl
            label={ __( "Arrow Border Radius" ) }
            value={ arrowBorderRadius }
            onChange={ ( value ) => setAttributes( { arrowBorderRadius: value } ) }
            min={ 0 }
            max={ 50 }
            />
            </Fragment>
        }
    </PanelBody>
        <PanelBody
                    title={__("Color", "responsive-gutenberg-addons")}
                    initialOpen={false}
                >
                    <p className="responsive-setting-label">
                        {__("Blog Post Background Color")}
                        <span className="components-base-control__label">
                            <span
                                className="component-color-indicator"
                                style={{ backgroundColor: attributes.bgColor }}
                            ></span>
                        </span>
                    </p>
                    <ColorPalette
                        value={attributes.bgColor}
                        onChange={value =>
                            this.props.setAttributes({
                                bgColor: value
                            })
                        }
                        allowReset
                    />
                    <p className="responsive-setting-label">
                        {__("Text Color")}
                        <span className="components-base-control__label">
                            <span
                                className="component-color-indicator"
                                style={{
                                    backgroundColor: attributes.textColor
                                }}
                            ></span>
                        </span>
                    </p>
                    <ColorPalette
                        value={attributes.textColor}
                        onChange={value =>
                            this.props.setAttributes({
                                textColor: value
                            })
                        }
                        allowReset
                    />
                </PanelBody>

            </InspectorControls>
        );
    }
}
