/**
 * BLOCK: Responsive Blocks Column
 */

// Import block dependencies and components
import Edit from "./components/edit";
import Save from "./components/save";

// Import CSS
import "./styles/style.scss";
import "./styles/styles.editor.scss";

// Internationalization
const { __ } = wp.i18n;

// Register block
const { registerBlockType } = wp.blocks;

// Register the block
registerBlockType("responsive-gutenberg-addons/column", {
        title: __("Column", "responsive-gutenberg-addons"),
    description: __(
        "This block is an immediate child of Advanced Columns.",
        "responsive-gutenberg-addons"
    ),
    icon: "layout",
    category: "responsive_gutenberg_addons",
    keywords: [
        __("calendar", "responsive-gutenberg-addons"),
        __("date", "responsive-gutenberg-addons"),
        __("responsive", "responsive-gutenberg-addons")
    ],
    attributes: {
        topMargin: {
            type: "number",
            default: "",
        },
        eventsDay: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-day",
            source: "children"
        },
        eventsMonth: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-month",
            source: "children"
        },
        eventsYear: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-year",
            source: "children"
        },
        eventsTitle: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-title",
            source: "children"
        },
        eventsDescription: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-description",
            source: "children"
        },
        eventsTime: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-time",
            source: "children"
        },
        eventsLocation: {
            type: "array",
            selector: ".responsive-gutenberg-addons-events-location",
            source: "children"
        }
    },

    /* Render the block in the editor. */
    edit: props => {
        return <Edit {...props} />;
    },

    /* Save the block markup. */
    save: props => {
        return <Save {...props} />;
    }
});
